import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const footerFormEl = document.querySelector('.footer-form');
const footerEmailInputEl = document.querySelector('#user-email');

footerFormEl.addEventListener('submit', onHandleFooterForm);

function onHandleFooterForm(event) {
  event.preventDefault();

  const email = footerEmailInputEl.value.trim();
  const emailPattern = /^\w+(\.\w+)?@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;

  // Ð’Ð°Ð»Ñ–Ð´Ð°Ñ†Ñ–Ñ email
  if (!emailPattern.test(email)) {
    iziToast.error({
      title: 'Error',
      message: 'Please enter a valid email address',
      position: 'topRight',
    });
    return;
  }

  // ÐžÑ‚Ñ€Ð¸Ð¼Ð°Ð½Ð½Ñ Ð¿Ñ–Ð´Ð¿Ð¸ÑÐ¾Ðº Ð· localStorage
  let subscribers = JSON.parse(localStorage.getItem('subscribers')) || [];

  // ÐŸÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ° Ð½Ð° Ð´ÑƒÐ±Ð»Ñ–ÐºÐ°Ñ‚Ð¸
  if (subscribers.includes(email)) {
    iziToast.warning({
      title: 'Notice',
      message: 'This email is already subscribed',
      position: 'topRight',
    });
    footerFormEl.reset();
    return;
  }

  // Ð—Ð±ÐµÑ€ÐµÐ¶ÐµÐ½Ð½Ñ Ð½Ð¾Ð²Ð¾Ð³Ð¾ email
  subscribers.push(email);
  localStorage.setItem('subscribers', JSON.stringify(subscribers));

  // ÐŸÐ¾Ð²Ñ–Ð´Ð¾Ð¼Ð»ÐµÐ½Ð½Ñ Ð¿Ñ€Ð¾ ÑƒÑÐ¿Ñ–ÑˆÐ½Ñƒ Ð¿Ñ–Ð´Ð¿Ð¸ÑÐºÑƒ
  iziToast.success({
    title: 'Success',
    message: 'Thank you for subscribing!',
    position: 'topRight',
  });

  // ÐžÑ‡Ð¸Ñ‰ÐµÐ½Ð½Ñ Ñ„Ð¾Ñ€Ð¼Ð¸
  footerFormEl.reset();
}
